import React, {useCallback, useEffect, useRef, useState} from 'react';
import SecondaryBtn from "../../layout/SecondaryBtn/SecondaryBtn";

import styles from "./HomeAboutDesign.module.scss"

import {
    homeDesign1,
    homeDesign2,
    homeDesign3,
    homeDesign4,
    homeDesign5,
} from "../../../assets/images";
import {designPagePath} from "../../../router/path";
import {scrollTop} from "../../../globalFunctions";
import {useNavigate} from "react-router-dom";

const imgArr = [
    homeDesign1,
    homeDesign2,
    homeDesign3,
    homeDesign4,
    homeDesign5,
]

function HomeAboutDesign() {
    const navigate = useNavigate()

    const onClickLearnMore = useCallback(
        () => {
            navigate(designPagePath)
            scrollTop()
        }
        // eslint-disable-next-line
        , [])

    return (
        <section className={`${styles['homeAboutDesign']} container`}>
            <h3 className={`${styles["homeAboutDesign__title"]} titleDef`}>Convenient<span
                className="lightBlueText"> UX/UI Design</span></h3>
            <div
                className={styles["homeAboutDesign__imagesBlock"]}
            >
                {imgArr.map((item, index) => (
                    <div className={styles["homeAboutDesign__card"]} key={index}>
                        <img
                            className={styles["homeAboutDesign__cardImg"]}
                            src={item}
                            alt="card"
                        />
                    </div>
                ))}
            </div>
            <SecondaryBtn className={styles["homeAboutDesign__btn"]} onClick={onClickLearnMore}>Learn
                More</SecondaryBtn>
        </section>

    )
        ;
}

export default HomeAboutDesign;